label {
    display: block; }

form {
    margin: 10px auto; }

h2 {
    color: rgb(61, 186, 204); }

input {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 16px;
    padding: 4px;
    font-size: 14px;
    border: 2px solid rgb(227, 224, 224);
    transition: .2s;
    color: #292929;
    border-radius: 10px;
    outline: none; }

input:focus {
    border: 2px solid rgb(61, 186, 204); }
