.App {
  position: relative;
  text-align: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden; }

body {
  background-color: #3dbacc;
  overflow: hidden; }

svg {
  visibility: hidden;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: visible;
  position: absolute; }

.pizzer {
  position: absolute;
  width: 80vh;
  height: 80vh;

  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: visible; }

.plater {
  position: absolute;
  width: 80vh;
  height: 80vh;
  top: 50%;
  left: -80vh;
  transform: translateY(-50%); }
